$base-font-family: 'Source Sans Pro', Tahoma, Helvetica, Arial, sans-serif !default;
$base-font-size:   16px !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

$text-color:       #34495e !default;
$background-color: #fdfdfd !default;
$grey-color:       #828282 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;

body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    margin:0;
}

a {
    text-decoration: none;
}

h1 {
    font-size: $base-font-size * 2.7;
    font-weight: 1;
    margin:10px 0px;
}

p a:link {
    color: #0047ab;
}
p a:visited {
    color: #0047ab;
}

li a:link {
  color: #0047ab;
}
li a:visited {
  color: #0047ab;
}

#page-container {
    position: relative;
    min-height: 100vh;
  }

.clearfix::after {
    content: "";
    clear: both;
    display: table;
    padding-bottom: 1rem;
  }

.container {
    max-width: 750px;
    margin: auto;
    padding-left:5vw;
    padding-right:5vw;
    display:block;
    position:relative;
    p {
        text-align: justify;
    }
}

/*@media (max-width: 1000px) {
  .container {
        padding-left:0;
