        max-width:90%;
   }
  }*/

.site-header {
    border-top: 5px solid $grey-color-dark;
    border-bottom: 1px solid $grey-color-light;
    min-height: 55px;
    position: relative;
  }
  .site-title {
    font-size: 30px;
    color: $grey-color-dark;
    line-height: $base-line-height * $base-font-size * 2.25;
    letter-spacing: -1px;
    margin-bottom: 0;
    padding-right: 1em;
    float: left;
  }
  .social {
      padding:5px;
      line-height: $base-line-height * $base-font-size * 2.25;
      margin-bottom: 0;
      display: inline-block;
      position: relative; 
      z-index: 1;
  }
  .social-span {
    display: inline-block;
  }
  .svg-object {
    position: relative; 
    z-index: -1;
    width: 1em;
    height: 1em;
    fill: #{$grey-color};
  }

  .site-nav {
    float: right;
    line-height: $base-line-height * $base-font-size * 2.25;
    a {
        margin-left: 20px;
    }
    a:link {
        color: $grey-color-dark;
        font-size: 1.3em;
    }
    a:visited {
        color: $grey-color-dark;
    }
  }

  .hover-underline-animation {
    display: inline-block;
    position: relative;
    color: $grey-color-dark;
  }
  .hover-underline-animation:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 14px;
    left: 0;
    background-color: $grey-color-dark;
    transform-origin: center;
    transition: transform 0.25s ease-out;
  }
  .hover-underline-animation:hover:after {
    transform: scaleX(1);
    transform-origin: center;
  }

.site-footer {
    border-top: 1px solid $grey-color-light;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1rem;
    p {
        text-align: center;
        padding-bottom: 30px;
    }
}